import $ from 'jquery';
import '../../../libs/slider/jquery-ui.min';
import '../../../libs/select/customselect.min';

$(document).ready(function () {
  $("#demo").customselect({
    "numitems": 1
  });

  // Select sort by

  const Select = function (node) {

    const self = this;
    self.node = node;
    self.list = [];
    self.text = "";

    const listItems = self.node.querySelectorAll(".form-select__item");

    self.init = function () {
      self.list = self.node.querySelector(".form-select__list");
      self.text = self.node.querySelector(".form-select__head");

      self.addListeners();
    };

    self.addListeners = function () {

      listItems.forEach(function (item) {
        item.addEventListener("click", function (event) {
          event.preventDefault();
          self.changeText(event.target);
        });
      });

      self.node.addEventListener('click', function (event) {
        self.open(event.target);
      });
    };

    self.open = function (node) {
      event.target.classList.add("arrow-up");
      if(node.nextElementSibling !== null) {
        node.nextElementSibling.classList.add("open");
      }
    };

    self.close = function () {
      self.list.classList.remove("open");
      self.text.classList.remove("arrow-up");
    };

    self.changeText = function (node) {
      self.text.textContent = node.textContent;
      self.close();
    };
  };

  const selectArr = document.querySelectorAll("[data-type='select']");
  for (let i = 0; i < selectArr.length; i++) {
    const select = new Select(selectArr[i]);
    select.init();
  }

  $(document).on('click', function (e) {
    if(!$(e.target).closest("[data-type='select']").length) {
      $(".form-select__list").removeClass("open");
      $(".form-select__head").removeClass("arrow-up");
    }
    e.stopPropagation();
  });

  //  toggle

  const filterButtons = document.querySelectorAll(".filter-button");

  function toggleFields() {
    this.classList.toggle("open");

    const panel = this.nextElementSibling;
    if(panel.style.display === "none") {
      panel.style.display = "block";
    } else {
      panel.style.display = "none";
    }
  }

  for (let i = 0; i < filterButtons.length; i++) {
    filterButtons[i].addEventListener("click", toggleFields);
  }

  // range slider price

  /*
  * data-gfrom - min 
  * data-gto - max 
  * data-lfrom - min     
  * data-lto - max     
  * */

  $('#rangePrice').slider({
    range: true,
    step: 1000,
    min: $('#rangePrice').data('gfrom'),
    max: $('#rangePrice').data('gto'),
    values: [$('#rangePrice').data('lfrom'), $('#rangePrice').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromPrice').val(ui.values[0]);
      $('#rangeToPrice').val(ui.values[1]);
    }
  });

  $('#rangeFromPrice').val($('#rangePrice').slider('values', 0));
  $('#rangeToPrice').val($('#rangePrice').slider('values', 1));

  // range slider year

  $('#rangeYear').slider({
    range: true,
    step: 1,
    min: $('#rangeYear').data('gfrom'),
    max: $('#rangeYear').data('gto'),
    values: [$('#rangeYear').data('lfrom'), $('#rangeYear').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromYear').val(ui.values[0]);
      $('#rangeToYear').val(ui.values[1]);
    }
  });

  $('#rangeFromYear').val($('#rangeYear').slider('values', 0));
  $('#rangeToYear').val($('#rangeYear').slider('values', 1));

  //  range slider length

  $('#rangeLength').slider({
    range: true,
    step: 1,
    min: $('#rangeLength').data('gfrom'),
    max: $('#rangeLength').data('gto'),
    values: [$('#rangeLength').data('lfrom'), $('#rangeLength').data('lto')],
    slide: function (event, ui) {
      $('#rangeFromLength').val(`${ (ui.values[0] * 3.2808).toFixed(0) }ft/${ ui.values[0] }m`);
      $('#rangeToLength').val(`${ (ui.values[1] * 3.2808).toFixed(0) }ft/${ ui.values[1] }m`);
    }
  });

  $('#rangeFromLength').val(`${ ($('#rangeLength').slider('values', 0) * 3.2808).toFixed(0) }ft/${ $('#rangeLength').slider('values', 0) }m`);
  $('#rangeToLength').val(`${ ($('#rangeLength').slider('values', 1) * 3.2808).toFixed(0) }ft/${ $('#rangeLength').slider('values', 1) }m`);


  //num +1

  const addBasket = function (node) {
    const self = this;
    self.node = node;

    const plus = self.node.querySelector("[data-plus]");
    const minus = self.node.querySelector("[data-minus]");
    const sum = self.node.querySelector("[data-sum]");


    self.init = function () {
      self.plus();
      self.minus();
    };

    self.plus = function () {
      plus.addEventListener("click", function () {
        if(+sum.textContent >= 0) {
          sum.textContent = +sum.textContent + 1;
        }
      });
    };

    self.minus = function () {
      minus.addEventListener("click", function () {
        if(+sum.textContent >= 1) {
          sum.textContent = +sum.textContent - 1;
        }
      });
    };
  };

  const basket = document.querySelectorAll("[data-add]");

  for (let i = 0; i < basket.length; i++) {
    const add = new addBasket(basket[i]);
    add.init();
  }

  //  toggle map
  $(".catalog-map__btn").click(function () {
    $(".catalog-map").toggleClass("open");
  });

  //  data picker
  $(function () {
    let dateFormat = "Medium";

    let from = $("#from")
      .datepicker({
        defaultDate: "11 Feb, 2020",
        dateFormat: "d M, yy",
        changeMonth: false,
        numberOfMonths: 1
      })
      .on("change", function () {
        to.datepicker("option", "minDate", getDate(this));
      });
    let to = $("#to").datepicker({
      defaultDate: "11 Feb, 2021",
      dateFormat: "d M, yy",
      changeMonth: false,
      numberOfMonths: 1
    })
      .on("change", function () {
        from.datepicker("option", "maxDate", getDate(this));
      });

    function getDate(element) {
      let date;
      try {
        date = $.datepicker.parseDate(dateFormat, element.value);
      } catch (error) {
        date = null;
      }

      return date;
    }
  });

});

